import style from './showMessage.module.less'
import getComponentRootDom from './getComponentRootDom.js'
import Icon from '../components/Icon'
/**
 * 
 * @param {Object} param0 
 * {
 * warn info success errorinfo
 * 2000,
 * ( )
 *   transitionend}
 */
export default function showMessage({ content, type = 'info', duration = 2000, container, callback } = {}) {
    const div = document.createElement('div');
    const comp = getComponentRootDom(Icon, {
            type,
        })
        // console.log(container)
        // console.log(comp);
        // getComponentRootDomdom
    div.innerHTML = `<div class=${style.icon}>${comp.outerHTML}</div><div>${content}</div>`;
    div.className = `${style.message} ${style["message-" + type]}`;

    const test = container;
    container = container || document.body;
    const conStyle = getComputedStyle(container);
    if (test) {
        if (conStyle.position === 'static') {
            container.style.position = 'relative';
        }
    }
    container.appendChild(div);
    // 
    div.clientHeight;

    div.style.transform = 'translate(-50%, -50%)';
    div.style.opacity = 1;

    // duration
    setTimeout(() => {
        div.style.transform = 'translate(-50%, -50%) translateY(-25px)';
        div.style.opacity = 0;
        div.addEventListener('transitionend', () => {
            div.remove();
            callback && callback();
        }, { once: true })
    }, duration)

}