import request from  './request.js'
export async function getBlogs(page = 1,limit = 20,categoryid = -1){
    return await request.get('/api/blog',
    { params:{
            page,
            limit,
            categoryid,
        },
    })
}

export async function getBlogTypes(){
    return await request.get('/api/blogtype')
}

// 
/**
 * 
 * @param {string} id id 
 * @returns 
 */
export async function getBlog(id){
    return await request.get(`/api/blog/${id}`);
}

//  post 
/**
 * 
 * @param {Object} param body
 */
export async function postComment(param){
    return await request.post('/api/comment',param);
}

// 
export async function getComments(page = 1,limit = 10,blogId = -1){
    return await request.get('/api/comment',
    { params:{
            page,
            limit,
            blogId,
        },
    })
}
