import imgUrl from '@/assets/loading.svg'
import styles from './loading.module.less'
function createImgLoading(){
    const img = document.createElement('img');
    img.src=imgUrl;
    // 
    img.dataset.loading = 'loading';
    img.className=styles.loading;
    return img;
}
function getImgLoading(){
    return document.querySelector('img[data-loading]');
}
export default function(el,binding){
    const imgLoading = getImgLoading();
    if(binding.value){
        //  el  
        if(!imgLoading){
            el.appendChild(createImgLoading());
        }
        
    }else{
        // 
        if(imgLoading){
            imgLoading.remove();
        }
    }
}