import Vue from 'vue'
/**
 * 
 * @param {*} comp  
 * @param {*} props  
 */
export default function getComponentRootDom(comp, props) {
    const vm = new Vue({
            render: (h) => {
                return h(comp, { props })
            }
        })
        // console.log(comp)
    vm.$mount();
    return vm.$el;
}