Mock.mock(/^\/api\/blog(\?.+)?$/,'get',function(options){
	const urlStr = qs.parse(options.url);
	
	return Mock.mock({
		code: 0,
		msg: "",
		data: { 
			total:2500,
			[`rows|${urlStr.limit  || 20}`]: [ 
				{
					'id':'@guid',
					'title': "@ctitle(1,50)",
					'description': "@cparagraph(1,10)",
					'category': { 
						'id|1-10': 0,
						'name': "@id"
					},
					'scanNumber|0-3000': 0,
					'commentNumber|0-300': 0,
					'thumb|1': ["https://wx1.sinaimg.cn/mw2000/002R448Kgy1gwrlbpwkjfj61y11y1u0x02.jpg",null],
					'createDate': '@date(T)'
				}
			]
		}})
})

